type ButtonProps = {
  className: string,
  id: string,
  value: string,
  onClick: (value: string) => void,
};
const Button = (props: ButtonProps) => {
  return (
    <button className={props.className} id={props.id} onClick={() => props.onClick(props.value)}>
      {props.value}
    </button>
  );
};

export default Button;