import './Calculator.scss';
import Button from "./Button";
import {useState} from "react";

const isOperator = (value: string): boolean => {
  const numbers = ['/', 'X', '-', '+'];
  return numbers.indexOf(value) !== -1;
};

const isNumber = (value: string): boolean => {
  return /^\d$/.test(value);
};

const Calculator = () => {
  const [total, setTotal] = useState('');
  const [current, setCurrent] = useState('0');
  const [timer, setTimer] = useState<NodeJS.Timeout>();
  const [error, setError] = useState('');
  const [reset, setReset] = useState(true);

  const displayError = (err: string) => {
    setError(err);
    if (timer) {
      clearTimeout(timer);
    }
    const newTimer = setTimeout(() => {
      setError('');
    }, 1500);
    setTimer(newTimer);
  };

  const onClick = (value: string) => {
    setReset(false);
    if (isNumber(value) || value === '.') {
      if (current.length >= 20) {
        displayError('DIGIT LIMIT MET');
        return;
      }
    }
    if (total.indexOf('=') !== -1) {
      if (value === 'AC') {
        setTotal('');
        setCurrent('0');
        setReset(true);
      } else if (value === '=') {
        setTotal(`${current}=`);
      } else if (isOperator(value)) {
        setTotal(current);
        setCurrent(value);
      } else if (value === '.') {
        setTotal('');
        setCurrent('0.');
      } else {
        setTotal('');
        setCurrent(value);
      }
      return;
    }
    if (isOperator(value)) {
      const last = total[total.length - 1];
      if (!isOperator(current)) {
        setTotal(total + current);
      } else if (value === '-' && !isOperator(last)) {
        setTotal(total + current);
      } else if (value !== '-' && isOperator(last)) {
        setTotal(total.slice(0, total.length - 1));
      }
      setCurrent(value);
    } else if (isNumber(value)) {
      if (isOperator(current)) {
        setTotal(total + current);
        setCurrent(value);
      } else {
        if (current === '0') {
          setCurrent(value);
        } else
          setCurrent(current + value);
      }
    } else if (value === '.') {
      if (isOperator(current)) {
        setTotal(total + current);
        setCurrent('0.');
      } else if (current.indexOf('.') === -1) {
        setCurrent(current + '.');
      }
    } else if (value === 'AC') {
      setReset(true);
      setTotal('');
      setCurrent('0');
    } else if (value === '=') {
      let OGExpr = total + current;
      if (isOperator(OGExpr[OGExpr.length - 1])) {
        OGExpr = OGExpr.slice(0, OGExpr.length - 1);
      }
      let expr = (OGExpr).replace('X', '*').replace('--', '+');
      if (expr[expr.length - 1] === '.') {
        expr = expr + '0';
      }
      const result = eval(expr);
      setTotal(OGExpr + '=');
      setCurrent(`${result}`);
    }
  };

  const hasError = error.length !== 0;
  const actualTotal = (total + current).replaceAll('X', 'â‹…');
  return (
    <div className='Calculator'>
      <div id='screen'>
        <div id='total'>{reset ? '' : actualTotal}</div>
        <div id='display'>{hasError ? error : current}</div>
      </div>
      <Button id='clear' className='' value='AC' onClick={onClick}/>
      <Button className='operator' id='divide' value='/' onClick={onClick}/>
      <Button className='operator' id='multiply' value='X' onClick={onClick}/>
      <Button id='equals' className='' value='=' onClick={onClick}/>
      <Button className='number' id='seven' value='7' onClick={onClick}/>
      <Button className='number' id='eight' value='8' onClick={onClick}/>
      <Button className='number' id='nine' value='9' onClick={onClick}/>
      <Button className='operator' id='subtract' value='-' onClick={onClick}/>
      <Button className='number' id='four' value='4' onClick={onClick}/>
      <Button className='number' id='five' value='5' onClick={onClick}/>
      <Button className='number' id='six' value='6' onClick={onClick}/>
      <Button className='operator' id='add' value='+' onClick={onClick}/>
      <Button className='number' id='one' value='1' onClick={onClick}/>
      <Button className='number' id='two' value='2' onClick={onClick}/>
      <Button className='number' id='three' value='3' onClick={onClick}/>
      <Button className='number' id='zero' value='0' onClick={onClick}/>
      <Button className='number' id="decimal" value='.' onClick={onClick}/>
    </div>
  );
};

export default Calculator;
